/* ──────────────────────────────────────────────────────────
   DataTable toolbar + date-filter + pagination
   ────────────────────────────────────────────────────────── */

.dt-toolbar{
  display:flex;
  flex-wrap:wrap;
  align-items:center;
  gap:12px;
  margin-bottom:14px;
}

/* search box */
.dt-toolbar input[type="text"],
.dt-toolbar input[placeholder*="Search"]{
  padding:6px 10px;
  border:1px solid #cbd5e1;
  border-radius:6px;
  font-size:.85rem;
  flex:1 1 240px;
  min-width:200px;
}

/* quick-range buttons + custom range */
.dt-datebar{
  display:flex;
  align-items:center;
  gap:6px;
  flex-wrap:nowrap;
}
.dt-datebar button{
  border:none;
  background:#e2e8f0;
  color:#334155;
  padding:6px 12px;
  border-radius:6px;
  font-size:.78rem;
  cursor:pointer;
  transition:background .15s;
  white-space:nowrap;
}
.dt-datebar button:hover{ background:#cbd5e1; }
.dt-datebar button.active{
  background:#0ea5e9;
  color:#fff;
}

/* custom range inputs */
.dt-datebar .custom{
  display:inline-flex;
  align-items:center;
  gap:6px;
  margin-left:6px;
  white-space:nowrap;
}
.dt-datebar input[type="date"]{
  padding:6px 10px;
  border:1px solid #cbd5e1;
  border-radius:6px;
  font-size:.78rem;
  background:#fff;
}

/* ──────────────────────────────────────────────────────────
   Table base (desktop)
   ────────────────────────────────────────────────────────── */
.table-container{ overflow-x:auto; box-sizing:border-box; }

.table-container table{
  width:100%;
  border-collapse:collapse;
  border-spacing:0;
  font-size:.92rem;
  table-layout:auto;
}

.table-container th,
.table-container td{
  padding:8px 12px;
  border-bottom:1px solid #e2e8f0;
  text-align:left;
  vertical-align:top;
  word-break:break-word;
  overflow-wrap:anywhere;
  white-space:normal;
  max-width:100%;
}

.table-container thead th{
  background:#f1f5f9;
  font-weight:600;
}

.table-container .status-badge,
.table-container .view-btn,
.table-container button{
  white-space:normal;
}

.no-data{
  text-align:center;
  padding:16px;
  color:#64748b;
}

/* ──────────────────────────────────────────────────────────
   Pagination
   ────────────────────────────────────────────────────────── */
.dt-pagination{
  display:flex;
  align-items:center;
  gap:8px;
  margin:12px 0;
  flex-wrap:wrap;
}

.dt-pagination button{
  border:none;
  background:#e2e8f0;
  color:#334155;
  padding:4px 10px;
  border-radius:6px;
  font-size:.8rem;
  cursor:pointer;
}
.dt-pagination button:disabled{
  background:#f1f5f9;
  color:#94a3b8;
  cursor:not-allowed;
}
.dt-pagination select{
  padding:4px 8px;
  border:1px solid #cbd5e1;
  border-radius:6px;
  background:#fff;
  font-size:.8rem;
}

/* ──────────────────────────────────────────────────────────
   MOBILE ( ≤ 768px ) — zero horizontal scroll
   ────────────────────────────────────────────────────────── */
@media (max-width: 768px){

  /* Toolbar stacks; no horizontal scroll */
  .dt-toolbar{
    flex-direction:column;
    align-items:stretch;
    gap:10px;
  }

  /* Datebar wraps */
  .dt-datebar{
    flex-wrap:wrap;
    row-gap:8px;
  }
  .dt-datebar .custom{
    white-space:normal;
    width:100%;
    display:flex;
    gap:8px;
  }
  .dt-datebar .custom input{
    flex:1 1 0;
    min-width:0;
  }

  /* Hard clamp any table to viewport & convert to cards */
  .table-container{ overflow-x:hidden; max-width:100vw; }
  .table-container table{ display:block; width:100%; border-spacing:0; }
  .table-container thead{ display:none; }
  .table-container tbody,
  .table-container tr{ display:block; width:60%; min-width:0; }

  .table-container tbody tr{
    border:1px solid #e5e7eb;
    border-radius:12px;
    padding:12px;
    margin-bottom:10px;
    background:#fff;
    box-shadow: 0 1px 0 rgba(0,0,0,0.02);
  }

  .table-container td{
    display:grid;
    grid-template-columns: 42% 1fr;   /* label / value */
    gap:10px;
    padding:6px 0;
    border:0;
    word-break:break-word;
    overflow-wrap:anywhere;
    max-width:100%;
  }
  .table-container td + td{ border-top:1px dashed #eef2f7; }

  /* Labels for mobile cards: use data-th when present */
  .table-container td::before{
    content: attr(data-th);
    color:#475569;
    font-weight:600;
  }

  /* If cell has no data-th, collapse to single-column value (no blank left strip) */
  .table-container td:not([data-th]){ grid-template-columns: 1fr; }
  .table-container td:not([data-th])::before{ content:none; display:none; }

  .table-container td *{
    word-break:break-word;
    overflow-wrap:anywhere;
    max-width:100%;
  }

  /* footer wraps & spaces nicely */
  .dt-pagination{
    justify-content: space-between;
    row-gap:8px;
  }
}

/* Extra guard */
@media (max-width: 768px){
  html, body { overflow-x:hidden; }
}
