/* ========== Modern Auth Styles (BNBWU palette) ========== */

:root{
  /* brand pulled from logo */
  --brand-maroon: #5D2F39;
  --brand-gold:   #C3AC6B;

  --card: #ffffff;
  --muted: #6b7280;        /* slate-500 */
  --text: #0f172a;         /* slate-900 */
  --primary: var(--brand-maroon);
  --primary-hover: #4A222A;
  --ring: rgba(93, 47, 57, 0.25);
  --border: #e5e7eb;       /* gray-200 */
  --error-bg: #fef2f2;     /* red-50 */
  --error-fg: #991b1b;     /* red-800 */
  --shadow: 0 10px 36px rgba(2, 6, 23, 0.12);
}

/* Wrapper for new Login */
.auth-wrapper.simple{
  min-height: calc(100vh - 160px); /* space for global header/footer */
  display: grid;
  place-items: center;
  padding: 24px;
  background: transparent;
}

.auth-card{
  width: 100%;
  max-width: 420px;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 28px 28px 22px;
  box-shadow: var(--shadow);
}

/* Title */
.title-group{
  text-align: center;
  margin-bottom: 16px;
}
.title-group h2{
  margin: 6px 0 4px;
  color: var(--text);
  font-size: 1.45rem;
  letter-spacing: -0.02em;
}
.title-group p{
  margin: 0;
  color: var(--muted);
  font-size: 0.95rem;
}

/* Error */
.status-bar{
  margin: 14px 0;
  padding: 10px 12px;
  border-radius: 10px;
  font-size: 0.95rem;
  border: 1px solid #fecaca;
}
.status-bar.error{
  background: var(--error-bg);
  color: var(--error-fg);
}

/* Form */
.auth-form{
  display: grid;
  gap: 12px;
  margin-top: 8px;
}

.input-field{
  position: relative;
  display: flex;
  align-items: center;
}

.input-field input{
  width: 100%;
  padding: 12px 44px 12px 40px;
  border: 1px solid var(--border);
  border-radius: 10px;
  font-size: 0.98rem;
  color: var(--text);
  background: #fff;
  transition: border-color .2s, box-shadow .2s, background .2s;
}
.input-field input::placeholder{ color: #9aa3af; }
.input-field input:focus{
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 4px var(--ring);
  background: #ffffff;
}

.input-icon{
  position: absolute;
  left: 12px;
  color: #94a3b8;
  display: grid;
  place-items: center;
  pointer-events: none;
}

.icon-btn{
  position: absolute;
  right: 8px;
  display: grid;
  place-items: center;
  width: 32px;
  height: 32px;
  border: 1px solid var(--border);
  background: #fff;
  color: #64748b;
  border-radius: 8px;
  cursor: pointer;
  transition: background .2s, color .2s, border-color .2s, transform .05s;
}
.icon-btn:hover{ background: #f8fafc; color: var(--text); }
.icon-btn:active{ transform: scale(0.98); }

.btn-primary{
  width: 100%;
  padding: 12px;
  background: var(--primary);
  color: #fff;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background .25s, transform .05s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.btn-primary:hover{ background: var(--primary-hover); }
.btn-primary:active{ transform: translateY(1px); }
.btn-primary:disabled{ opacity: .8; cursor: not-allowed; }

/* Spinner */
.spin{ animation: spin 1s linear infinite; }
@keyframes spin{ to { transform: rotate(360deg); } }

/* Links */
.helper-links{
  margin-top: 16px;
  text-align: center;
  color: var(--muted);
  font-size: 0.95rem;
}
.helper-links a{
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
}
.helper-links a:hover{ text-decoration: underline; }
.helper-links .dot{ margin: 0 6px; color: #cbd5e1; }

/* ===== Compatibility styles for older pages (Signup / Forgot) ===== */
/* We intentionally DO NOT hide .auth-container anymore. */
.auth-container {
  background: #ffffff;
  border: 1px solid var(--border);
  border-radius: 12px;
  box-shadow: var(--shadow);
  padding: 28px 28px 22px;
  max-width: 520px;
  margin: 60px auto;
}
.auth-container h2 { margin-bottom: 20px; color: var(--text); }
.auth-container .form-group { margin-bottom: 16px; }
.auth-container label { display:block; margin-bottom: 6px; color: var(--muted); font-weight: 600; }
.auth-container input,
.auth-container select {
  width: 100%;
  padding: 12px;
  border: 1px solid var(--border);
  border-radius: 10px;
  font-size: 0.98rem;
}
.auth-container input:focus,
.auth-container select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 4px var(--ring);
}
.auth-container button{
  width: 100%;
  padding: 12px;
  background: var(--primary);
  color: #fff;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background .25s;
}
.auth-container button:hover{ background: var(--primary-hover); }
.auth-container .footer-text { text-align: center; margin-top: 14px; color: var(--muted); }
.auth-container .footer-text a { color: var(--primary); font-weight: 600; text-decoration: none; }
.auth-container .footer-text a:hover { text-decoration: underline; }
