/* ─────────────────────────  F I X   W I D T H  ───────────────────────── */
.split-layout{display:flex; min-height:calc(100vh - 70px);}          /* 70 = header */
.split-layout .table-wrapper{
  flex:1;                       /* ← grow to fill remaining space      */
  min-width:0;                  /* ← allow the flex item to shrink in small view-ports */
  padding:16px 24px;
  display:flex; flex-direction:column;    /* so footer sticks to bottom if desired */
}

.table-container{width:100%; overflow-x:auto;}
.table-container table{width:100%; min-width:900px;}
/* Optional: keep sticky header flush with sidebar on scroll */
.table-container thead{position:sticky; top:0; z-index:2;}