/*  lightweight overrides only for analytics  */

/* horizontal date-range bar */
.lookback{
  margin-bottom:12px;
}
.lookback button{
  border:none;background:#e2e8f0;color:#334155;
  padding:6px 12px;border-radius:6px;margin-right:6px;
  cursor:pointer;font-size:.85rem;
}
.lookback button.active{background:#0ea5e9;color:#fff;}

.lookback input[type="date"]{
  padding:6px 10px;border:1px solid #cbd5e1;border-radius:6px;
  font-size:.85rem;
}

/* layout that mixes sidebar + main dash */
/* ─────────────────────────  F I X   W I D T H  ───────────────────────── */
.split-layout{display:flex; min-height:calc(100vh - 70px);}          /* 70 = header */
.split-layout .table-wrapper{
  flex:1;                       /* ← grow to fill remaining space      */
  min-width:0;                  /* ← allow the flex item to shrink in small view-ports */
  padding:16px 24px;
  display:flex; flex-direction:column;    /* so footer sticks to bottom if desired */
}

.table-container{width:100%; overflow-x:auto;}
.table-container table{width:100%; min-width:900px;}
/* Optional: keep sticky header flush with sidebar on scroll */
.table-container thead{position:sticky; top:0; z-index:2;}
/* sidebar */
.aside{
  width:190px;background:#0f172a;color:#fff;min-height:100vh;
  display:flex;flex-direction:column;padding:20px 14px;
}
.aside-title{margin:0 0 14px;font-size:1.1rem;}
.anav{
  display:flex;align-items:center;gap:8px;
  color:#cbd5e1;text-decoration:none;padding:8px 6px;border-radius:6px;
  font-size:.9rem;margin-bottom:4px;
}
.anav.active, .anav:hover{background:#1e293b;color:#fff;}
.ani{font-size:1.1rem;}

/* kpi boxes already styled by Dashboard.css */

/* chart-area tweak */
.chart-card canvas{max-height:320px}

/* ──────────────────────────────────────────────────────────────
   MOBILE / TABLET  (≤ 768 px)
   ──────────────────────────────────────────────────────────────*/
@media(max-width:768px){

  /* 1) Sidebar moves to top, becomes a header-bar */
  .split-layout{
    flex-direction:column;
  }
  .aside{
    width:100%;
    min-height:auto;
    flex-direction:row;
    align-items:center;
    overflow-x:auto;        /* swipe to see items if many */
    gap:6px;
    padding:10px 12px;
  }
  .aside-title{margin:0 12px 0 0;}
  .anav{
    margin:0;
    padding:6px 10px;
    font-size:.8rem;
    white-space:nowrap;
  }

  /* 2) Main area now full-width */
  .split-layout .table-wrapper,
  .analytics-wrapper{
    padding:12px 16px;
  }

  /* 3) Look-back / date-bar */
  .lookback,
  .dt-datebar{
    flex-wrap:wrap;
    gap:8px;
  }
  .lookback button,
  .dt-datebar button{
    padding:5px 10px;
    font-size:.78rem;
  }
  .lookback input[type="date"],
  .dt-datebar input[type="date"]{
    font-size:.78rem;
    padding:5px 8px;
  }

  /* 4) KPI boxes re-flow 2-per-row */
  .kpi-row{
    grid-template-columns:repeat(auto-fit,minmax(140px,1fr));
  }

  /* 5) Chart height shrinks a bit */
  .chart-card canvas{
    max-height:260px;
  }

  /* 6) Tables: allow horizontal scroll without forcing min-width */
  .table-container table{
    min-width:600px;        /* was 900 –­­ smaller phone-friendly */
  }

  /* 7) Pagination footer: tighter */
  .dt-pagination span{font-size:.78rem;}
  .dt-pagination button,
  .dt-pagination select{
    font-size:.78rem;
    padding:4px 8px;
  }
}

/* ─── HAMBURGER / DRAWER  ─────────────────────────────────────── */
.burger-btn{
  display:none;                            /* default hidden */
  position:fixed; top:14px; left:14px;
  z-index:9999;
  background:#0f172a; color:#f8fafc;
  border:none; border-radius:6px;
  padding:6px; cursor:pointer;
}

/* backdrop when drawer open */
.shade{
  position:fixed; inset:0; z-index:9990;
  background:rgba(0,0,0,.35);
}

/* === mobile breakpoint === */
@media (max-width:768px){
  /* show burger */
  .burger-btn{display:block;}

  /* slide-in drawer */
  .aside{
    position:fixed; inset:0 auto 0 0;      /* left dock */
    width:220px; height:100vh;
    transform:translateX(-100%);
    transition:transform .28s ease;
    box-shadow:2px 0 6px rgba(0,0,0,.25);
    z-index:9995;
  }
  .aside.open{ transform:translateX(0); }

  /* when drawer overlays, give main area full width  */
  .split-layout,
  .analytics-wrapper,
  .table-wrapper{ padding-left:0 !important; }
}
