/* =======================================================================
   0.  DESIGN TOKENS
   =======================================================================*/
:root{
  /* GK sidebar browns */
  --br-900:#3b0909; --br-700:#601515; --br-600:#742020;

  /* neutrals */
  --g-050:#fafafa; --g-075:#f8f8f8; --g-100:#f5f7fa; --g-150:#eef1f4;
  --g-200:#e1e4e8; --g-300:#ccd0d5; --g-400:#cbd5e1; --g-600:#475569;

  /* accents */
  --blue-600:#2563eb; --blue-700:#1e40af;
  --green-600:#059669; --green-700:#047857;
  --orange-500:#f97316; --orange-600:#f59e0b;
  --red-600:#dc2626;
  --yellow-400:#facc15;
}

/* global reset */
*{box-sizing:border-box;margin:0} body{font-family:Arial,Helvetica,sans-serif;}

/* =======================================================================
   1.  CONTAINERS & HEADER
   =======================================================================*/
.dashboard-container{max-width:1000px;margin:40px auto;padding:24px;background:#fff;
  border-radius:8px;box-shadow:0 2px 12px rgba(0,0,0,.1);}
.dashboard-header{display:flex;flex-wrap:wrap;align-items:center;
  justify-content:space-between;gap:16px;margin-bottom:24px;}
.dashboard-header h2{font-size:1.75rem;font-weight:600;}
/* counters */
.counters{display:flex;gap:12px;flex-wrap:wrap;}
.counter-item{min-width:90px;padding:8px 14px;border-radius:8px;text-align:center;
  background:var(--g-100);}
.counter-item div:first-child{font-size:.85rem;color:var(--g-600);}
.counter-item div:last-child{font-size:1.25rem;font-weight:700;margin-top:4px;}

.counter-pending  { background:#fef3c7; }  /* soft-orange   */
.counter-approved { background:#d1fae5; }  /* soft-green    */
.counter-rejected { background:#fee2e2; }  /* soft-red      */
/* =======================================================================
   2.  FILTERS / BUTTONS
   =======================================================================*/
.search-filter{display:flex;flex-wrap:wrap;gap:12px;margin:16px 0;}
.search-filter input, .search-filter select{flex:1 1 200px;padding:8px 12px;
  border:1px solid var(--g-300);border-radius:6px;font-size:.9rem;}

.period-btn,.quick-range button{padding:6px 14px;border:none;border-radius:6px;
  background:var(--g-100);font-size:.85rem;cursor:pointer;transition:.15s;}
.period-btn.active,.quick-range .active{background:var(--blue-600);color:#fff;}
.period-btn:hover:not(.active){background:#fff;color:var(--br-600);
  border:1px solid var(--br-600);}

.action-btn{padding:8px 20px;border:none;border-radius:8px;font-weight:600;
  font-size:.9rem;color:#fff;background:var(--blue-600);cursor:pointer;transition:.15s;}
.action-btn:hover{background:var(--blue-700);}
.action-btn.green{background:var(--green-600);} .action-btn.green:hover{background:var(--green-700);}
.action-btn.blue {background:var(--blue-600);}  /* explicit variant */

.view-btn{padding:4px 12px;border:none;border-radius:6px;background:var(--blue-600);
  color:#fff;font-size:.8rem;cursor:pointer;transition:.15s;}
.view-btn:hover{background:var(--blue-700);}

.save-btn{padding:6px 18px;border:none;border-radius:6px;background:var(--blue-600);
  color:#fff;font-size:.88rem;cursor:pointer;} .save-btn:hover{background:var(--blue-700);}
.cancel-btn,.cancel-btn-form{padding:6px 18px;border:none;border-radius:6px;
  background:var(--g-100);cursor:pointer;font-size:.88rem;}
.cancel-btn:hover,.cancel-btn-form:hover{background:var(--g-200);}

/* =======================================================================
   3.  TABLES
   =======================================================================*/
.table-container{width:100%;overflow-x:auto;background:#fff;border-radius:10px;
  box-shadow:0 2px 6px rgba(0,0,0,.05);}
table{width:100%;border-collapse:collapse;font-size:.85rem;}
th,td{padding:10px 14px;border-bottom:1px solid var(--g-200);text-align:left;}
th{background:var(--g-050);font-weight:600;font-size:.78rem;text-transform:uppercase;color:var(--g-600);}
tbody tr:nth-child(even){background:var(--g-150);}
tbody tr:hover{background:#eef3f8;}
.no-data{padding:16px;text-align:center;font-style:italic;color:var(--g-600);}

.status-badge{display:inline-block;padding:4px 10px;border-radius:20px;font-size:.75rem;
  font-weight:600;text-transform:uppercase;color:#fff;}
.status-pending {background:var(--orange-600);}
.status-approved{background:var(--green-600);}
.status-rejected{background:var(--red-600);}
.status-out     {background:var(--orange-500);}
.status-in      {background:var(--green-600);}

.actions{display:flex;gap:8px;flex-wrap:wrap;}

/* =======================================================================
   4.  PAGINATION
   =======================================================================*/
.pagination{display:flex;gap:10px;align-items:center;margin:18px 0;flex-wrap:wrap;}
.pagination button{padding:6px 14px;border:none;border-radius:6px;background:var(--g-100);
  cursor:pointer;font-size:.8rem;} .pagination button:disabled{opacity:.45;cursor:default;}
.page-info{font-size:.8rem;color:var(--g-600);}

/* =======================================================================
   5.  MODAL  ( **improved “View Request”** )
   =======================================================================*/
.modal-overlay{position:fixed;inset:0;background:rgba(0,0,0,.5);
  display:flex;align-items:center;justify-content:center;z-index:1000;}
.modal{background:#fff;border-radius:12px;width:92%;max-width:720px;display:flex;
  flex-direction:column;box-shadow:0 8px 30px rgba(0,0,0,.25);overflow:hidden;
  animation:pop .25s;}
/* coloured accent left-border if data-status attribute is set */
.modal[data-status="pending"]  {border-left:8px solid var(--orange-600);}
.modal[data-status="approved"] {border-left:8px solid var(--green-600);}
.modal[data-status="rejected"] {border-left:8px solid var(--red-600);}

.modal-header{padding:20px 28px;background:var(--g-050);border-bottom:1px solid var(--g-200);
  position:sticky;top:0;}
.modal-body  {padding:26px 28px;max-height:70vh;overflow-y:auto;}
.modal-footer{padding:18px 28px;background:var(--g-050);border-top:1px solid var(--g-200);
  display:flex;gap:10px;justify-content:flex-end;position:sticky;bottom:0;}

.field{margin-bottom:14px;} .field label{font-weight:600;color:var(--g-600);display:block;margin-bottom:5px;}
.comment-input{width:100%;padding:10px 12px;border:1px solid var(--g-300);border-radius:6px;font-size:.9rem;}

.form-group{display:flex;flex-direction:column;gap:6px;margin-bottom:16px;}
.form-group input,.form-group select{padding:10px 12px;border:1px solid var(--g-300);
  border-radius:6px;font-size:.9rem;}

/* =======================================================================
   6.  GATE-KEEPER LAYOUT
   =======================================================================*/
.gk-wrapper{display:flex;min-height:100vh;}
.gk-sidebar{width:220px;background:var(--br-900);color:#fff;display:flex;flex-direction:column;
  gap:6px;padding:40px 22px;transition:transform .28s;z-index:998;}
.gk-sidebar h2{margin:22px 0 26px 0;font-weight:700;}
.gk-title{font-size:.75rem;font-weight:700;margin:16px 0 4px;color:var(--g-050);
  letter-spacing:.05em;text-transform:uppercase;}
.gk-sidebar button{display:flex;align-items:center;gap:8px;padding:10px 18px;margin:0 6px;
  border:none;background:transparent;color:#fff;border-radius:6px;cursor:pointer;transition:.15s;}
.gk-sidebar button:hover{background:var(--br-700);} .gk-sidebar button.active{background:var(--br-600);}
@media(max-width:768px){.gk-sidebar{position:fixed;top:0;left:0;height:100%;transform:translateX(-100%);} .gk-sidebar.open{transform:translateX(0);}}

.gk-hamburger{position:fixed;top:12px;left:12px;padding:6px;border:none;border-radius:6px;
  background:var(--br-600);color:#fff;z-index:999;display:none;}
@media(max-width:768px){.gk-hamburger{display:block;}}

.gk-main{flex:1;padding:32px 48px;min-width:0;}
@media(max-width:600px){.gk-main{padding:68px 16px 32px;}}

/* =======================================================================
   7.  MOBILE “CARD” ROWS (all dashboards)
   =======================================================================*/
@media(max-width:480px){
  table,thead,tbody,th,td,tr{display:block;}
  thead{position:absolute;top:-9999px;left:-9999px;}
  tr{margin:10px 0;background:#fff;border:1px solid var(--g-200);border-radius:10px;
     box-shadow:0 2px 6px rgba(0,0,0,.06);}
  td{display:flex;justify-content:space-between;gap:10px;padding:10px 14px;
     border:none;border-bottom:1px solid var(--g-200);}
  td:last-child{border:none;}
  td::before{content:attr(data-label);font-weight:600;color:var(--g-600);}
}

/* =======================================================================
   8.  LOADING OVERLAY
   =======================================================================*/
.loading-box{position:fixed;inset:0;display:flex;align-items:center;justify-content:center;
  background:rgba(255,255,255,.7);font-size:1.2rem;font-weight:600;z-index:1100;}

  /* ===== analytics layout ====== */
.analytics-wrapper   { padding:24px; display:flex; flex-direction:column; gap:24px; }
.lookback button     { margin-right:8px; padding:6px 14px; border-radius:6px; border:1px solid #ddd; }
.lookback .active    { background:#2563eb; color:#fff; border:1px solid #2563eb; }
.kpi-row             { display:grid; grid-template-columns:repeat(auto-fill,minmax(140px,1fr)); gap:12px; }
.kpi                 { padding:16px; border-radius:8px; color:#fff; text-align:center; }
.kpi h3              { margin:0; font-size:1.6rem; line-height:1; }
.kpi p               { margin:2px 0 0; font-size:.8rem; }
.chart-grid          { display:grid; grid-template-columns:1fr 320px; gap:24px; }
.chart-card          { background:#fff; padding:16px; border-radius:8px; box-shadow:0 1px 4px rgba(0,0,0,.05); }
.dash-sidebar nav a  { display:flex; align-items:center; gap:8px; padding:10px 12px; border-radius:6px; }
.dash-sidebar nav a.active { background:#1e3a8a; color:#fff; }

/* let cells wrap naturally and keep table responsive */
.gk-main .table-container td{
  word-break: break-word;
}

.error        { color:#e11d48; font-size:12px; }
.gh-btn.thumb img { width:48px; height:48px; object-fit:cover; border-radius:4px; }

.error            { color:#e11d48; font-size:12px; }
.gh-btn.thumb img { width:48px; height:48px; object-fit:cover; border-radius:4px; }

.error {
  color:#e11d48;
  font-size:12px;
}

.hint  { font-size:12px; color:#64748b; font-weight:400; }
.error { color:#e11d48; font-size:12px; }

.hint  { font-size:12px; color:#64748b; font-weight:400; }
.error { color:#e11d48; font-size:12px; }

/* ===== Sleek details modal (Gatekeeper "View") ===== */
.details-modal .modal.sleek{
  max-width: 820px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 18px 42px rgba(2,6,23,0.22);
  background: #fff;
}

/* Header */
.details-modal .details-header{
  display:flex; align-items:center; justify-content:space-between;
  padding: 14px 18px;
  background: #f8fafc;              /* soft top */
  border-bottom: 1px solid #e5e7eb;
}
.details-modal .details-header h3{
  margin:0; font-size: 1.1rem; font-weight: 700; color:#111827;
}
.details-modal .icon-close{
  border:none; background:transparent; font-size: 22px; line-height: 1;
  color:#6b7280; cursor:pointer; padding: 0 6px;
}
.details-modal .icon-close:hover{ color:#111827; }

/* Summary strip under header */
.details-modal .summary-strip{
  display:flex; align-items:flex-start; justify-content:space-between;
  gap: 12px;
  padding: 14px 18px 10px;
  border-bottom: 1px solid #f1f5f9;
}
.details-modal .summary-title{
  display:flex; align-items:center; flex-wrap:wrap; gap:10px;
}
.details-modal .student-name{
  font-size: 1.05rem; font-weight: 700; color:#111827;
}
.details-modal .chip-id{
  font-size: .85rem; color:#334155; background:#e2e8f0; border-radius:999px;
  padding: 2px 8px;
}
.details-modal .summary-sub{
  margin-top: 6px; color:#475569; font-size: .92rem;
  display:flex; align-items:center; gap:10px; flex-wrap:wrap;
}
.details-modal .chip-hostel{
  font-size: .82rem; color:#fff; background:#5D2F39; /* brand maroon */
  border-radius: 999px; padding: 2px 8px;
  text-transform: capitalize;
}
.details-modal .summary-right{ display:flex; align-items:center; }

/* Body grid */
.details-modal .modal-body{ padding: 18px; }
.details-modal .kv-grid{
  display:grid; gap: 14px 18px;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
.details-modal .kv{
  display:grid; grid-template-columns: 160px 1fr; gap: 10px;
  align-items: start;
}
.details-modal .kv.full{ grid-column: 1 / -1; }
.details-modal .k{
  font-size: .8rem; font-weight: 600; color:#6b7280; text-transform: uppercase;
  letter-spacing: .03em;
}
.details-modal .v{ color:#111827; }
.details-modal .prewrap{ white-space: pre-wrap; word-break: break-word; }
.details-modal .mono{ font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace; }
.details-modal .note{
  background:#f8fafc; border:1px solid #e5e7eb; border-radius: 8px;
  padding: 10px 12px;
}

/* Footer */
.details-modal .modal-footer{
  padding: 12px 18px; border-top: 1px solid #f1f5f9;
}

/* Make it comfy on small screens */
@media (max-width: 700px){
  .details-modal .kv-grid{ grid-template-columns: 1fr; }
  .details-modal .kv{ grid-template-columns: 120px 1fr; }
}
