/* ===== Brand palette from the logo ===== */
:root{
  --brand-maroon: #5D2F39;   /* deep maroon ring */
  --brand-gold:   #C3AC6B;   /* gold rim */
  --brand-brown:  #8E6A5E;   /* inner warm brown */
  --ink:          #1f2937;   /* neutral text */
  --muted:        #6b7280;

  /* Watermark sizing (shared by both lines so widths match) */
  --wm-size: clamp(28px, 6vw, 76px);          /* main "BNBWU" size */
  --wm-width: clamp(220px, 22vw, 520px);      /* exact width used by both lines */
  --wm-sub-size: calc(var(--wm-size) * 0.28); /* subtitle font size */
  --wm-opacity: 0.18;                         /* overall subtlety */
}

/* Layout base */
html, body { height: 100%; margin: 0; }
body {
  font-family: 'Segoe UI', Tahoma, sans-serif;
  background: #fff; /* keep page clean */
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* ===== Header ===== */
.app-header {
  position: relative;                /* for the watermark layers */
  background: #ffffff;
  border-bottom: 1px solid #ece7dd;  /* soft beige line */
}

/* Top gold bar */
.brand-bar {
  height: 4px;
  width: 100%;
  background: linear-gradient(90deg, var(--brand-gold), #e7d7a0 40%, var(--brand-gold));
}

/* Watermark: MAIN line (BNBWU) */
.app-header::after{
  content: 'BNBWU';
  position: absolute;
  top: 10px;
  right: 200px;
  width: var(--wm-width);           /* << exact width we’ll reuse below */
  text-align: right;
  font-weight: 800;
  letter-spacing: 0.18em;
  font-size: var(--wm-size);
  line-height: 0.95;
  color: var(--brand-YELLOW);
  opacity: var(--wm-opacity);
  pointer-events: none;
  user-select: none;
  mix-blend-mode: multiply;         /* merge into bg */
  z-index: 0;                       /* behind header content */
  white-space: nowrap;
}

/* Watermark: SUBTITLE lines beneath (exactly same width) */
.app-header::before{
  content: 'BEGUM NUSRAT BHUTTO\A WOMEN UNIVERSITY SUKKUR';
  position: absolute;
  /* place just below the main watermark; multiplier tuned for visual rhythm */
  top: calc(10px + var(--wm-size) * 0.92);
  right: 200px;
  width: var(--wm-width);           /* << matches main line width exactly */
  text-align: right;
  font-weight: 800;
  letter-spacing: 0.16em;
  font-size: var(--wm-sub-size);
  line-height: 1.15;
  color: var(--brand-YELLOW);
  opacity: calc(var(--wm-opacity) - 0.001); /* a hair softer */
  pointer-events: none;
  user-select: none;
  mix-blend-mode: multiply;
  z-index: 0;
  white-space: pre;                 /* respect \A line break */
}

.header-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 24px;
  position: relative;
  z-index: 1;                        /* above watermark */
}

.header-left {
  display: flex;
  align-items: center;
  gap: 14px;
}

/* Logo — wider, non-rounded */
.app-logo {
  width: 120px;
  height: auto;
  object-fit: contain;
  border-radius: 0;
  border: none;
  display: block;
}

.title-wrap { display: flex; flex-direction: column; }
.app-title {
  margin: 0;
  font-size: 1.65rem;
  font-weight: 700;
  letter-spacing: -0.01em;
  color: var(--brand-maroon);
}

.current-user {
  margin: 6px 0 0;
  font-size: 0.95rem;
  color: var(--muted);
}

.role-chip {
  margin-left: 8px;
  padding: 2px 8px;
  font-size: 0.8rem;
  color: #fff;
  background: var(--brand-brown);
  border-radius: 999px;
  vertical-align: middle;
}

/* Logout button */
.logout-button {
  background: var(--brand-maroon);
  border: 1px solid var(--brand-maroon);
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background .2s, color .2s, transform .05s, box-shadow .2s;
}
.logout-button:hover {
  background: #4A222A; /* darker maroon */
  box-shadow: 0 6px 16px rgba(93,47,57,0.18);
}
.logout-button:active { transform: translateY(1px); }

/* ===== Main ===== */
.app-content { flex: 1; }

/* ===== Footer ===== */
.app-footer {
  background: var(--brand-maroon);
  color: #ffffff;
  padding: 14px 16px;
}
.footer-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.footer-inner a {
  color: #fff;
  text-decoration: underline;
  text-underline-offset: 3px;
}
.footer-inner a:hover {
  color: #f5e8bf; /* light gold hover */
}
.separator { opacity: .6; }

/* ----- Mobile Responsive ----- */
@media (max-width: 768px) {
  .header-inner { flex-direction: column; gap: 12px; text-align: center; }
  .header-left { flex-direction: column; }
  .app-logo { width: 160px; }
  .app-title { font-size: 1.35rem; }
  .current-user { font-size: 0.9rem; }
  .logout-button { width: 100%; max-width: 220px; }

  /* Hide watermark & subtitle on small screens to avoid clutter */
  .app-header::after,
  .app-header::before { content: none; }
  .footer-inner { padding: 0 8px; }
}
/* ==== Mobile overflow kill & box model safety ==== */
*,
*::before,
*::after { box-sizing: border-box; }

@media (max-width: 768px){
  html, body { width:100%; max-width:100vw; overflow-x:hidden; }

  /* clamp common wrappers to viewport */
  .app-container, .app-content, main,
  .split-layout, .table-wrapper,
  .dashboard-container, .gk-wrapper, .gk-main,
  .table-container, .prettified {
    max-width:100vw;
    overflow-x:hidden;
  }
}
